import { Injectable } from '@angular/core';
import { FCM } from '@ionic-native/fcm';
import { Storage } from '@ionic/storage';
import { AngularFireAuth } from 'angularfire2/auth';
import { ToastController } from 'ionic-angular';
import { Platform } from 'ionic-angular/platform/platform';
import 'rxjs/add/operator/map';
import { User } from '../../models/user';
import { ContentProvider } from '../content/content';

@Injectable()
export class AppConfigProvider {

  mainTrack = 'TenOase';
  tracks: Array<string> = [`${this.mainTrack}`];

  private user: any = {} as User;

  constructor(
    private fcm: FCM,
    private storage: Storage,
    private platform: Platform,
    private afAuth: AngularFireAuth,
    private cntProvider: ContentProvider,
    private toast: ToastController,
  ) {
    this.subscribeToTopic(`${this.mainTrack}`);
    this.setarrangement(`${this.mainTrack}`);

    this.storage.set('arrangement', `${this.mainTrack}`);
    storage.get('tracks').then((tracks) => {
      if (tracks != null) {
        this.tracks = tracks;
      }
    });
    this.checkAdmin();
    if (this.platform.is('cordova')) {
      this.fcm.getToken().then(() => {
        // Just make sure that we are subscribed to the maintrack-channel.
        this.fcm.subscribeToTopic(`${this.mainTrack}`);
        if (this.user.admin) {
          this.fcm.subscribeToTopic('admin').catch((e) => console.log(e));
        } else {
          this.fcm.unsubscribeFromTopic('admin');
        }
      });
    }
  }

  removeTrack(track: any): any {
    const pos = this.tracks.indexOf(track);
    if (pos !== -1) { // Track exist in array
      this.tracks.splice(pos, 1);
      this.unsubscribeFromTopic(track);
      this.toast.create({
        message: `${track} abbonement avsluttet`,
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 2000,
        position: 'top'
      }).present();
    }
    // save new tracks state
    this.storage.set('tracks', this.tracks);
  }

  getTracks(): Array<string> {
    return this.tracks;
  }

  clearAllTracks(): void {
    this.tracks = [];
    this.storage.set('tracks', this.tracks);
  }

  isSubscribedToTrack(track): boolean {
    return (this.tracks.indexOf(track) !== -1);
  }

  addTrack(track: any): any {
    const pos = this.tracks.indexOf(track);
    if (pos === -1) { // Track does not exist in array
      this.tracks.push(track);
      this.subscribeToTopic(track);
      this.toast.create({
        message: `Følger nå ${track}`,
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 2000,
        position: 'top'
      }).present();
    }
    // save new tracks state
    this.storage.set('tracks', this.tracks);
  }

  public checkAdmin() {
    this.afAuth.authState.subscribe((data) => {
      this.user.uid = data.uid;
      this.storage.set('uid', data.uid);
      // Check if user is admin
      this.cntProvider.userIsAdmin(data.uid).then((res) => {
        this.user.admin = res;
      });
    });
  }

  public subscribeToTopic(topic: string): void {
    if (this.platform.is('cordova')) {
      this.fcm.subscribeToTopic(topic).then((data) => {
        console.log(data);
      }).catch((error) => {
        console.error(error);
      });
    }
  }

  public unsubscribeFromTopic(topic: string): void {
    if (this.platform.is('cordova')) {
      this.fcm.unsubscribeFromTopic(topic).catch((error) => console.log(error));
    }
  }

  public getSubscriptions(): object[] {
    return this.user.pushSubscriptions;
  }

  public getUser() {
    return this.user;
  }

  public setUser(user) {
    this.user = user;
    this.storage.set('user', user);
  }

  public setarrangement(arrangement) {
    if (this.platform.is('cordova')) {
      this.fcm.getToken().then(() => {
        this.storage.get('arrangement').then((oldarrangement) => {
          if (oldarrangement !== arrangement) {
            this.fcm
              .unsubscribeFromTopic(`${oldarrangement}`)
              .catch((e) => console.log(e))
              .then(() => {
                this.fcm
                  .subscribeToTopic(`${arrangement}`)
                  .catch((e) => console.log(e));
              });
          } else {
            this.fcm.subscribeToTopic(`${arrangement}`).catch((e) => console.log(e));
          }
        });
      });
    } else {
      this.storage.set('arrangement', arrangement);
    }
  }

  public getArrangement() {
    const promise = new Promise((resolve, reject) => {
      this.storage.get('arrangement').then((arrangement) => {
        resolve(arrangement);
      });
    });
    return promise;
  }

}



