import {Injectable} from '@angular/core';
import {Headers, Http} from '@angular/http';
import {AngularFireAuth} from 'angularfire2/auth';
import 'rxjs/add/operator/map';
import {User} from '../../models/user';

@Injectable()
export class ContentProvider {
  public user: any = {} as User;
  // private serverURL = 'http://localhost:8080';
  private serverURL = 'https://oase-api.herokuapp.com';

  constructor(private http: Http, private afAuth: AngularFireAuth) {
    this.afAuth.authState.subscribe((data) => {
      this.user.uid = data.uid;
    });
  }

  postPerson(person: any): any {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .post(`${this.serverURL}/api/person`, JSON.stringify(person), {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  delPerson(updatedData: any): any {
    return new Promise((resolve, reject) => {
      console.log('Deleting Event');
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .delete(`${this.serverURL}/api/person/${updatedData._id}`, {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  updatePerson(updatedData): any {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .put(
          `${this.serverURL}/api/person/${updatedData._id}`,
          JSON.stringify(updatedData),
          {headers},
        )
        .subscribe((res) => {
          console.log(res);
          resolve(res);
        });
    });
  }

  importData(events): any {
    /// TODO: NOTE this is very hacky way to quickly add data to database. Do NOT use this in a production environment!
    // const data = [];
    let updated = 0;
    let posted = 0;
    events.forEach(event => {
      if (!event._id) {
        // console.log(`New event ${event.title}`);
        this.postEvent(event).then(() => {
          posted++;
        });
      } else {
        // console.log(`Update ${event.title}`);
        this.updateEvent(event);
        updated++;
      }
    });
    console.log(`Out of ${events.length}\n Tried to update ${updated} events and \n tried to post ${posted} events.`);
  }

  public currentAppVersion(): any {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/currentAppVersion`)
        .map((res) => res.json())
        .subscribe((NewestAppVersion) => {
          resolve(NewestAppVersion);
        });
    });
  }

  public postMessage(messageData) {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .post(`${this.serverURL}/api/messages`, JSON.stringify(messageData), {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  public postEvent(eventData) {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .post(`${this.serverURL}/api/events`, JSON.stringify(eventData), {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  public async getMessagesForTracks(tracks: Array<string>, category: string): Promise<Array<string>> {
    const allMessages = [];
    await tracks.forEach(async track => {
      await this.getMessages(track, category).then((messages) => {
        messages.forEach(message => {
          allMessages.push(message);
        });
        allMessages
          .sort((a, b) => {
            return new Date(b.datetime).getTime() - new Date(a.datetime).getTime();
          });
      });
    });
    return allMessages;
  }

  public async getEventsForTracks(tracks: Array<string>): Promise<Array<any>> {
    const allEvents = [];
    await tracks.forEach(async track => {
      await this.getEventsForArrangement(track).then(async events => {
        events.forEach(event => {
          allEvents.push(event);
        });
        await allEvents
          .sort((a, b) => {
            return new Date(a.fromTime).getTime() - new Date(b.fromTime).getTime();
          });
      });
    });
    return allEvents;
  }

  /**
   * Get Messages for arrangement with category
   *
   * @param {string} arrangement
   * @param {string} category
   * @returns promise with posts sorted by date
   * @memberof ContentProvider
   */
  public getMessages(arrangement: string, category: string): any {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/${arrangement}/messages/${category}`)
        .map((res) => res.json())
        .subscribe((posts) => {
          posts.sort((a, b) => {
            return (
              new Date(a.fromTime).getTime() - new Date(b.fromTime).getTime()
            );
          });
          resolve(posts);
        });
    });
  }

  /**
   * Get Upcoming events for arrangement
   *
   * @param {string} arrangement
   * @returns
   * @memberof ContentProvider
   */
  public getUpcomingEvents(arrangement: string) {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/${arrangement}/events/upcoming`)
        .map((res) => res.json())
        .subscribe((data) => {
          data.sort((a, b) => {
            return (
              new Date(a.fromTime).getTime() - new Date(b.fromTime).getTime()
            );
          });
          resolve(data);
        });
    });
  }

  public getAllEvents() {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/events`)
        .map((res) => res.json())
        .subscribe((data) => {
          data.sort((a, b) => {
            return (
              new Date(a.fromTime).getTime() - new Date(b.fromTime).getTime()
            );
          });
          resolve(data);
        });
    });
  }

  public getEventsForArrangement(arrangement: string): any {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/${arrangement}/events`)
        .map((res) => res.json())
        .subscribe((data) => {
          data.sort((a, b) => {
            return (
              new Date(a.fromTime).getTime() - new Date(b.fromTime).getTime()
            );
          });
          resolve(data);
        });
    });
  }

  public getMessagesForArrangementWithCategory(arrangement, category) {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/${arrangement}/messages/${category}`)
        .map((res) => res.json())
        .subscribe((posts) => {
          resolve(posts);
        });
    });
  }

  public getEventsForArrangementWithCategory(arrangement, category) {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/${arrangement}/events/${category}`)
        .map((res) => res.json())
        .subscribe((posts) => {
          resolve(posts);
        });
    });
  }

  public deleteEvent(updatedData): any {
    return new Promise((resolve, reject) => {
      console.log('Deleting Event');
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .delete(`${this.serverURL}/api/events/${updatedData._id}`, {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
    });
  }


  public deleteMessage(updatedData): any {
    const promise = new Promise((resolve, reject) => {
      console.log('Deleting Message');
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .delete(`${this.serverURL}/api/messages/${updatedData._id}`, {
          headers,
        })
        .subscribe((res) => {
          resolve(res);
        });
      return promise;
    });
  }

  public updateMessage(updatedData): any {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .put(
          `${this.serverURL}/api/messages/${updatedData._id}`,
          JSON.stringify(updatedData),
          {headers},
        )
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  public updateEvent(updatedData): any {
    return new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${this.user.uid}`);
      this.http
        .put(
          `${this.serverURL}/api/events/${updatedData._id}`,
          JSON.stringify(updatedData),
          {headers},
        )
        .subscribe((res) => {
          resolve(res);
        });
    });
  }

  public getArrangements(): any {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/arrangements`)
        .map((res) => res.json())
        .subscribe((data) => {
          resolve(data);
        });
    });
  }

  public userIsAdmin(userID): any {
    const promise = new Promise((resolve, reject) => {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('authorization', `Bearer ${userID}`);
      this.http
        .post(`${this.serverURL}/api/user/auth`, {},
          {headers})
        .map((res) => res.json())
        .subscribe((data) => {
          resolve(data);
        });
      return promise;
    });
    return promise;
  }

  public getTeam(team: string): any {
    return new Promise((resolve, reject) => {
      this.http
        .get(`${this.serverURL}/api/persons/team/${team}`)
        .map((res) => res.json())
        .subscribe((data) => {
          console.log(data);
          resolve(data);
        });
    });
  }

  private getTime(date?: Date) {
    return date != null ? date.getTime() : 0;
  }
}



